@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Varela Round', 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
  position: relative;
  overflow-x: hidden; /* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}

/* Î¶¨ÎùºÏø†Îßà Î∞∞Í≤Ω - ÍøÄÏÉâ Í∑∏ÎùºÎç∞Ïù¥ÏÖò */
.App.dark-mode {
  background: 
    radial-gradient(circle at 20% 30%, rgba(255, 198, 88, 0.2) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(139, 69, 19, 0.15) 0%, transparent 50%),
    linear-gradient(135deg, #8B4513 0%, #A0522D 50%, #CD853F 100%);
  color: #FFC658;
}

.App.light-mode {
  background: 
    radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.5) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 198, 88, 0.3) 0%, transparent 50%),
    linear-gradient(135deg, #FFF8DC 0%, #FFEFD5 50%, #FFE4B5 100%);
  color: #8B4513;
}

/* ÍøÄ Î∞©Ïö∏ Îç∞ÏΩîÎ†àÏù¥ÏÖò */
.App::before {
  content: 'üçØ';
  position: absolute;
  top: 50px;
  left: 100px;
  font-size: 80px;
  opacity: 0.1;
  animation: honeyDrop 8s ease-in-out infinite;
  pointer-events: none;
}

.App::after {
  content: 'üß∏';
  position: absolute;
  bottom: 80px;
  right: 120px;
  font-size: 100px;
  opacity: 0.15;
  animation: bearFloat 6s ease-in-out infinite;
  pointer-events: none;
}

@keyframes honeyDrop {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(20px) rotate(10deg); }
}

@keyframes bearFloat {
  0%, 100% { transform: translateY(0px) rotate(-5deg); }
  50% { transform: translateY(-15px) rotate(5deg); }
}

.App.timer-active.dark-mode {
  background: linear-gradient(135deg, #A0522D 0%, #CD853F 100%);
}

.App.timer-active.light-mode {
  background: linear-gradient(135deg, #FFF8DC 0%, #FFEFD5 100%);
}

.container {
  max-width: 800px;
  width: 100%;
  position: relative;
  z-index: 1;
  padding-bottom: 50px;
}

/* Ìó§Îçî */
.header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
}

.header h1 {
  font-size: 3em;
  margin-bottom: 10px;
  font-weight: 700;
  text-shadow: 3px 3px 0px rgba(139, 69, 19, 0.2);
}

.subtitle {
  font-size: 1.2em;
  opacity: 0.9;
  font-weight: 600;
}

.header-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 25px;
  flex-wrap: wrap;
}

.icon-btn {
  background: #FFC658;
  border: 4px solid #8B4513;
  border-radius: 20px;
  width: 55px;
  height: 55px;
  font-size: 1.6em;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 0px #8B4513;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-btn::after {
  content: 'üçØ';
  position: absolute;
  top: -12px;
  right: -12px;
  font-size: 1.2em;
  opacity: 0;
  transition: all 0.3s;
}

.icon-btn:hover {
  transform: translateY(-3px) rotate(-3deg);
  box-shadow: 0 9px 0px #8B4513;
}

.icon-btn:hover::after {
  opacity: 1;
  animation: honeyBounce 0.5s ease infinite;
}

@keyframes honeyBounce {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.icon-btn:active {
  transform: translateY(3px);
  box-shadow: 0 3px 0px #8B4513;
}

/* Î∞∞ÎÑà */
.dday-banner {
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  border: 4px solid #8B4513;
  border-radius: 25px;
  padding: 18px 25px;
  text-align: center;
  color: #8B4513;
  font-size: 1.3em;
  margin-bottom: 18px;
  box-shadow: 0 6px 0px #8B4513;
  font-weight: 700;
  position: relative;
}

.dday-banner::before {
  content: 'üêª';
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.8em;
}

.streak-banner {
  background: linear-gradient(135deg, #CD853F 0%, #DEB887 100%);
  border: 4px solid #8B4513;
  border-radius: 25px;
  padding: 18px 25px;
  text-align: center;
  color: white;
  font-size: 1.3em;
  margin-bottom: 18px;
  box-shadow: 0 6px 0px #8B4513;
  font-weight: 700;
  position: relative;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.streak-banner::before {
  content: 'üß∏';
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.8em;
}

/* Ï†ïÎ≥¥ Ïπ¥Îìú */
.quick-info {
  display: flex;
  gap: 12px;
  margin-bottom: 22px;
}

.quick-info-item {
  flex: 1;
  background: #FFF8DC;
  border: 4px solid #CD853F;
  border-radius: 20px;
  padding: 16px;
  text-align: center;
  color: #8B4513;
  font-size: 0.95em;
  box-shadow: 0 5px 0px #CD853F;
  font-weight: 700;
  position: relative;
}

.dark-mode .quick-info-item {
  background: rgba(255, 198, 88, 0.25);
  border-color: #FFC658;
  box-shadow: 0 5px 0px #FFC658;
  color: #FFC658;
}

.quick-info-item span {
  display: block;
  opacity: 0.85;
  font-size: 0.9em;
  margin-bottom: 5px;
}

.quick-info-item strong {
  display: block;
  font-size: 1.3em;
}

/* Î™®ÏùòÍ≥†ÏÇ¨ Î≤ÑÌäº & Í≥µÏö© Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.mock-exam-btn {
  width: 100%;
  padding: 22px;
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  color: #8B4513;
  border: 5px solid #8B4513;
  border-radius: 30px;
  font-size: 1.4em;
  font-weight: 700;
  cursor: pointer;
  margin-bottom: 28px;
  transition: all 0.3s ease;
  box-shadow: 0 8px 0px #8B4513;
  position: relative;
}

.mock-exam-btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 0px #8B4513;
}

.mock-exam-btn:active {
  transform: translateY(4px);
  box-shadow: 0 4px 0px #8B4513;
}

/* Í≥ºÎ™© Ïπ¥Îìú */
.subject-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(145px, 1fr));
  gap: 18px;
  margin-bottom: 28px;
}

.subject-card {
  background: #FFF8DC;
  border: 4px solid;
  border-radius: 25px;
  padding: 28px 18px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 0px currentColor;
  position: relative;
}

.dark-mode .subject-card {
  background: rgba(255, 198, 88, 0.2);
}

.subject-card:hover {
  transform: translateY(-8px) rotate(-2deg);
  box-shadow: 0 10px 0px currentColor;
}

.subject-emoji {
  font-size: 3.5em;
  margin-bottom: 12px;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.1));
}

.subject-name {
  font-size: 1.3em;
  font-weight: 700;
  color: #8B4513;
  margin-bottom: 8px;
}

.dark-mode .subject-name {
  color: #FFC658;
}

.subject-time {
  font-size: 1.1em;
  font-weight: 700;
  color: #CD853F;
}

/* Ï†ïÎ≥¥ Î∞ïÏä§ */
.info-box {
  background: #FFF8DC;
  border: 4px solid #CD853F;
  border-radius: 25px;
  padding: 22px 28px;
  color: #8B4513;
  text-align: center;
  box-shadow: 0 6px 0px #CD853F;
  position: relative;
}

.dark-mode .info-box {
  background: rgba(255, 198, 88, 0.2);
  border-color: #FFC658;
  box-shadow: 0 6px 0px #FFC658;
  color: #FFC658;
}

.info-box::before {
  content: 'üß∏';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5em;
}

/* Î™®Îã¨ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(139, 69, 19, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(10px);
}

.modal-content {
  background: #FFF8DC;
  border: 6px solid #8B4513;
  border-radius: 35px;
  padding: 40px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 10px 0px #8B4513;
  animation: bearPop 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
  max-height: 90vh;
  overflow-y: auto;
}

@keyframes bearPop {
  0% { transform: scale(0); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

.modal-content h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #8B4513;
  font-size: 2.2em;
  font-weight: 700;
}

/* Ï§ÄÎπÑ ÌôîÎ©¥ & ÌÉÄÏù¥Î®∏ */
.ready-container, .timer-container {
  max-width: 600px;
  width: 100%;
  text-align: center;
}

.ready-subject {
  font-size: 3.5em;
  font-weight: 700;
  margin-bottom: 32px;
  color: #8B4513;
}
.dark-mode .ready-subject { color: #FFC658; }

.ready-countdown {
  font-size: 9em;
  font-weight: 700;
  margin-bottom: 22px;
  color: #8B4513;
  animation: bearHop 1s infinite;
}
.dark-mode .ready-countdown { color: #FFC658; }

@keyframes bearHop {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.15); }
}

.timer-display {
  background: #FFF8DC;
  border: 8px solid #CD853F;
  border-radius: 35px;
  padding: 65px 42px;
  text-align: center;
  margin-bottom: 32px;
  box-shadow: 0 10px 0px #CD853F;
  transition: all 0.3s ease;
  position: relative;
}

.dark-mode .timer-display {
  background: rgba(255, 198, 88, 0.3);
  border-color: #FFC658;
  box-shadow: 0 10px 0px #FFC658;
}

.time {
  font-size: 7em;
  font-weight: 700;
  color: #8B4513;
  font-variant-numeric: tabular-nums;
  line-height: 1;
}

.dark-mode .time { color: #FFC658; }

/* ÌÉÄÏù¥Î®∏ Ïª®Ìä∏Î°§ Î≤ÑÌäº */
.timer-controls {
  display: flex;
  gap: 18px;
  justify-content: center;
  position: relative;
  z-index: 1000;
}

.btn {
  padding: 18px 45px;
  font-size: 1.3em;
  font-weight: 700;
  border: 5px solid #8B4513;
  border-radius: 22px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 0px #8B4513;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 9px 0px #8B4513;
}

.btn-pause {
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  color: #8B4513;
}

.btn-stop {
  background: #CD853F;
  color: white;
}

/* Ïò§Î≤ÑÎ†àÏù¥ (ÏùºÏãúÏ†ïÏßÄ, ÏôÑÎ£å) */
.paused-overlay, .completion-overlay {
  position: fixed;
  top: 0; left: 0; width: 100vw; height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px);
}

.completion-overlay {
  background: linear-gradient(135deg, rgba(139, 69, 19, 0.95) 0%, rgba(205, 133, 63, 0.95) 100%);
  z-index: 200;
}

.pause-main {
  font-size: 4rem;
  font-weight: 900;
  color: #ffffff;
  margin-bottom: 20px;
  text-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.pause-tip {
  font-size: 1.5rem;
  color: #FFC658;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px 30px;
  border-radius: 50px;
  max-width: 80%;
  text-align: center;
}

.completion-message {
  color: #FFC658;
  font-size: 2.5em;
  font-weight: 700;
  text-align: center;
  padding: 0 40px;
  line-height: 1.6;
  white-space: pre-line;
}

/* ==========================================================================
   üìå Ïò§ÎãµÎÖ∏Ìä∏ & Î™®ÏùòÍ≥†ÏÇ¨ & Ìèº Ïä§ÌÉÄÏùº (New Features)
   ========================================================================== */

/* Í≥µÌÜµ Ìèº Ïä§ÌÉÄÏùº */
.wrong-form {
  background: #FFF8DC;
  border: 6px solid #CD853F;
  border-radius: 28px;
  padding: 30px;
  box-shadow: 0 8px 0px #CD853F;
}

.dark-mode .wrong-form {
  background: rgba(255, 198, 88, 0.2);
  border-color: #FFC658;
  box-shadow: 0 8px 0px #FFC658;
}

.form-group-wrong {
  margin-bottom: 25px;
}

.form-group-wrong label {
  display: block;
  font-size: 1.1em;
  font-weight: 700;
  margin-bottom: 10px;
  color: #8B4513;
}
.dark-mode .form-group-wrong label { color: #FFC658; }

/* Í≥ºÎ™© ÏÑ†ÌÉù Í∑∏Î¶¨Îìú */
.subject-select-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
}

.subject-select-btn {
  padding: 12px;
  border: 3px solid #CD853F;
  background: white;
  border-radius: 12px;
  color: #8B4513;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
}

.subject-select-btn.active {
  background: #FFC658;
  border-color: #8B4513;
  transform: scale(1.05);
  box-shadow: 0 4px 0 #8B4513;
}

/* Ï§ëÏöîÎèÑ ÏÑ†ÌÉù */
.importance-select {
  display: flex;
  gap: 10px;
}

.importance-select-btn {
  flex: 1;
  padding: 12px;
  border: 3px solid #CD853F;
  background: white;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  color: #8B4513;
}

.importance-select-btn.active {
  background: #FFC658;
  border-color: #8B4513;
  color: #8B4513;
}

/* ÏûÖÎ†• ÌïÑÎìú */
.question-number-input, .wrong-memo-textarea {
  width: 100%;
  padding: 15px;
  border: 3px solid #CD853F;
  border-radius: 15px;
  font-size: 1em;
  font-family: inherit;
  outline: none;
}

.dark-mode .question-number-input,
.dark-mode .wrong-memo-textarea {
  background: rgba(0,0,0,0.2);
  border-color: #FFC658;
  color: #FFC658;
}

/* ÏÇ¨ÏßÑ Ï∂îÍ∞Ä Î≤ÑÌäº Î∞è ÌîÑÎ¶¨Î∑∞ */
.photo-add-buttons {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.photo-add-btn {
  flex: 1;
  padding: 12px;
  background: #DEB887;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  color: #8B4513;
}

.photo-preview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.photo-preview-item {
  position: relative;
  width: 100%;
  padding-bottom: 100%; /* 1:1 ÎπÑÏú® */
  border-radius: 10px;
  overflow: hidden;
  border: 2px solid #CD853F;
}

.photo-preview-item img {
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  object-fit: cover;
}

.photo-remove-btn {
  position: absolute;
  top: 2px; right: 2px;
  background: rgba(255,0,0,0.7);
  color: white;
  border: none;
  border-radius: 50%;
  width: 20px; height: 20px;
  font-size: 12px;
  cursor: pointer;
  display: flex; align-items: center; justify-content: center;
}

/* Ïò§ÎãµÎÖ∏Ìä∏ Î¶¨Ïä§Ìä∏ */
.review-section {
  background: rgba(0,0,0,0.05);
  padding: 15px;
  border-radius: 15px;
  margin-bottom: 20px;
}

.review-start-btn {
  padding: 10px 15px;
  border-radius: 20px;
  border: none;
  background: #667eea;
  color: white;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.2s;
}

.review-start-btn.important {
  background: #FFC658;
  color: #8B4513;
}

.review-start-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.review-start-btn:not(:disabled):hover {
  transform: translateY(-2px);
}

.filter-bar, .subject-filter-bar {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.filter-btn, .subject-filter-btn {
  padding: 8px 16px;
  border-radius: 20px;
  border: 2px solid #CD853F;
  background: transparent;
  color: #8B4513;
  font-weight: 600;
  white-space: nowrap;
  cursor: pointer;
}

.dark-mode .filter-btn, .dark-mode .subject-filter-btn {
  border-color: #FFC658;
  color: #FFC658;
}

.filter-btn.active, .subject-filter-btn.active {
  background: #CD853F;
  color: white;
}

.dark-mode .filter-btn.active, .dark-mode .subject-filter-btn.active {
  background: #FFC658;
  color: #8B4513;
}

/* Ïò§Îãµ Ïπ¥Îìú (Î¶¨Ïä§Ìä∏ ÏïÑÏù¥ÌÖú) */
.wrong-card {
  background: white;
  border: 4px solid #CD853F;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 6px 0 rgba(0,0,0,0.1);
  position: relative;
}

.dark-mode .wrong-card {
  background: #2a2a2a; /* Îã§ÌÅ¨Î™®Îìú Ïπ¥Îìú Î∞∞Í≤Ω */
