  font-weight: 700;
  font-size: 0.9em;
}

.badge-unreviewed {
  color: #ff6b6b;
  font-weight: 700;
  font-size: 0.9em;
}

/* Î™®ÏùòÍ≥†ÏÇ¨ Í∏∞Î°ù Ï†êÏàò Í∑∏Î¶¨Îìú */
.exam-subjects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 10px;
}

/* Í∏∞ÌÉÄ Ïú†Ìã∏ */
.back-btn {
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  border: 4px solid #8B4513;
  color: #8B4513;
  padding: 10px 20px;
  border-radius: 15px;
  font-weight: 700;
  cursor: pointer;
}

/* Ïä§ÌÅ¨Î°§Î∞î Ïª§Ïä§ÌÖÄ */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  background: #CD853F;
  border-radius: 4px;
}
::-webkit-scrollbar-track {
  background: rgba(0,0,0,0.05);
}

/* Î∞òÏùëÌòï Ï°∞Ï†ï */
@media (max-width: 480px) {
  .wrong-card { padding: 15px; }
  .photo-preview-grid { grid-template-columns: repeat(3, 1fr); }
  .filter-bar { flex-wrap: nowrap; }
}